@import url(https://fonts.googleapis.com/css2?family=Baskervville&family=Jura:wght@700&display=swap);
html {
	background-color: var(--pb);
	font-size: 10px;
}

body {
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
   'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
   sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: '#0C3559';
   margin: 0;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

:root {
  --pb: #0C3559;
  --sb: #138BA5;
  --rd: #F21D1D;
  --wt: #f2f2f2;
  --font1: 'Baskervville', serif;
  --font2: 'Jura', sans-serif;
}

* {
  box-sizing: border-box;
}

h1 {
  font-family: 'Baskervville', serif;
  font-family: var(--font1);
  font-size: 8rem;
  text-align: right;
  color: #0C3559;
  color: var(--pb);
  margin-bottom: 6rem;
  display: block;
}

h2, h3, h4, h5,a {
   font-family: 'Jura', sans-serif;
   font-family: var(--font2);
}

h2 {
  font-size: 4rem;
  font-weight: 400;
  text-align: right;
  line-height: 1.75;
  color: #138BA5;
  color: var(--sb);
  margin-bottom: 6rem;
  display: block;  
}

h3 {
  font-weight: 400;
  font-size: 2.25rem;
  text-align: right;
  line-height: 1.75;
  color: #0C3559;
  color: var(--pb);
  display: block;
  margin: 1rem 0;
  padding: 0;
}

h4 {
   font-size: 2rem;
   font-weight: 600;
   margin: 1rem 0;
}

h5 {
   font-size: 1.5rem;
   font-weight: 400;
   margin-top: 1rem;
}

a {
  color: #F21D1D;
  color: var(--rd);
  text-decoration: none;
}

a:hover {
  color: #138BA5;
  color: var(--sb);
  transition: ease-in-out 0.3s;
}

.App {
  text-align: center;
  height: 100vh;
  display: grid;
  grid-template-columns: 20rem 1fr;
}
.navGrid {
   height: 100vh;
   width: 20rem;
   padding: 2.5rem;
}

.mainGrid {
   background-color: '#0C3559';
   height: 90%;
   display: grid;
   grid-template-rows: 25rem 15rem  5rem 5rem 5rem;
   grid-row-gap: 2rem;
   justify-content: center;
   align-content: space-between;
   position: fixed;
}

.logo {
   width: 15rem;
}

.jeuel {
   width: 15rem;
   border: solid 8px var(--rd);
   border-radius: 50%;
}

button {
   font-family: var(--font2);
   font-size: 2rem;
   background-color: transparent;
   color: var(--wt);
   border: none;
   cursor: pointer;
}

button:hover {
   color: var(--rd);
   /* border: 1px solid var(--sb); */
   font-size: 2.25rem;
   border-radius: 5px;
   /* box-shadow: 0 0 5px var(--rd); */
   transition: ease-in-out 0.5s ;
}

.navLink {
   color: var(--wt)
}
/* General ------------------------------------*/
.pagesGrid {
	min-height: 100vh;
	padding: 5rem 15% 5rem 5rem;
	display: grid;
	grid-template-rows: repeat(3, auto);
	justify-content: flex-end;
	align-content: flex-start;
}

/* About  -------------------------------------*/
.about {
	background-color: var(--wt);
}

/* Portfolio ------------------------------------*/
.portfolioGrid {
	background-color: var(--pb);
	padding: 5rem 5rem 5rem 10rem;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(35rem, 1fr));
	grid-template-rows: repeat(auto-fit, 35rem) 10rem;
	grid-gap: 5rem;
}

.cards {
	height: 35rem;
	border: 1px solid var(--wt);
	border-radius: 1rem;
}

.projectImg {
	background-size: cover;
	background-position: top center;
	width: 100%;
	height: 75%;
	border-radius: 1rem;
}

/* Contact ------------------------------------*/
.contact {
	background-color: var(--wt);
}

